import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			totalPrice: '158.85',
			is70: true,
			icons:[
				{
					title:'账单',
					url:'../../../../static/imgs/icon-1011.png'
				},
				{
					title:'我的银行卡',
					url:'../../../../static/imgs/icon-1012.png'
				},
				{
					title:'支付密码',
					url:'../../../../static/imgs/icon-1013.png'
				},
				{
					title:'提现',
					url:'../../../../static/imgs/icon-1014.png'
				}
			]
		};
	},
	components: {uniIcons,uniList,uniListItem},
	onLoad() {
		// 设置头部样式
		uni.setNavigationBarColor({
			frontColor:"#ffffff",
			backgroundColor:"#FE3B0B"
		})
	},
	onShow() {
		
	},
	methods:{
		// 点击事件
		doClick(index){
			switch (index){
				case 0:
				this.goRecord()
					break;
				case 1:
				this.goBankcard()
					break;
				case 2:
				this.goPayps()
					break;
				case 3:
				this.goCash()
					break;
				default:
					break;
			}
		},
		// 去账单记录
		goRecord(){
			uni.navigateTo({
				url:'/pages/middle/release/account/record/record'
			})
		},
		// 去银行卡页面
		goBankcard(){
			uni.navigateTo({
				url:'/pages/middle/release/account/bankcard/bankcard'
			})
		},
		// 去支付密码页面
		goPayps(){
			uni.navigateTo({
				url:'/pages/middle/release/account/payps/payps'
			})
		},
		// 提现
		goCash(){
			uni.navigateTo({
				url:'/pages/middle/release/account/cash/cash'
			})
		}
		
	}
}
